import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, onItemChange }) => {
  useEffect(() => {}, []);

  return (
    <tr>
      <th scope="row">{item.id}</th>
      <td>
        {item.editable ? (
          <Input
            type="number"
            value={item.num}
            onChange={event => onItemChange({ ...item, num: event.target.value })}
          />
        ) : (
          item.num
        )}
      </td>
      <td>
        {item.editable ? (
          <Input
            type="number"
            value={item.weigth}
            onChange={event => onItemChange({ ...item, weigth: event.target.value })}
          />
        ) : (
          item.weigth
        )}
      </td>
      <td>
        {/* <Button
          color={!item.editable ? 'primary' : 'danger'}
          onClick={
            !item.editable
              ? onItemChange({ ...item, editable: true })
              : onItemChange({ ...item, editable: false })
          }
        >
          {!item.editable ? 'edit' : 'done'}
        </Button> */}
      </td>
    </tr>
  );
};
