import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ title, data }) => {
  const [_data, setData] = useState(data);

  useEffect(() => {
    setData(
      _data.map(item => {
        return { ...item, editable: true };
      }),
    );
  }, []);

  const handleOnSave = () => {
    alert('saving');
  };

  const handleOnAdd = () => {
    setData([..._data, { id: _data.length + 1, num: 0, weigth: 0, editable: true }]);
  };

  const handleOnDelete = () => {
    setData(_data.filter(item => item.id !== _data.length));
  };

  const onItemChange = item => {
    setData(
      _data.map(serie => {
        if (serie.id === item.id) {
          return item;
        }
        return serie;
      }),
    );
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              {Object.keys(data[0]).map(c => (
                <th>{c}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {_data.map(item => (
              <Rep key={item.id} item={item} onItemChange={onItemChange}></Rep>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
